* {
  font-family: Helvetica, Arial, sans-serif;
}

h1 {
  font-size: 2em;
}

input {
  color: gray;
  font-size: 1.3em;
  width: 100%;
}

.cardGrid {
  width: 100%;
  display: flex;
  align-content: center;
  flex-wrap: wrap;
}

.card {
  width: 22%;
  margin: 1% 1%;
  border: 1px solid grey;
  border-radius: 6px;
}

.card img {
  width: 100%;
}

.card p {
  margin: 0 10px;
  text-align: center;
}